import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router, Route, NavLink, Switch} from 'react-router-dom';
import Navigate from './components/nav.jsx';
import Home from './components/Home.jsx';
import Examples from './components/examples.jsx';
import Author from './components/author.jsx';
import Conditioner from './components/conditioner.jsx';
import Timer from './components/Timer.jsx';
import LanguageForm from './components/List.jsx';
import BlurButton from './components/button.jsx';
import LoginForm from './components/SubmitLP.jsx';
import App from './components/srcWithForm/App.jsx';
import PersonForm from './components/PersoFormTar.jsx';
import FilterProduct from './components/search/Appsearch.jsx';
import AppNews from './components/AppNews.jsx';
import FileInput from './components/fileInput.jsx';
import NotFound from './components/notfound.jsx';



ReactDOM.render(<Router>
                    <div>
                        <Navigate />
                        <Switch>
                           
                            <Route exact path="/myfirstpage1.github.io/"
                                component={Home}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples"
                                component={Examples}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/conditioner"
                                component={Conditioner}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/timer"
                                component={Timer}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/list"
                                component={LanguageForm}
                                />
                            <Route path="/myfirstpage1.github.io/author"
                                component={Author}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/button"
                                component={BlurButton}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/lp"
                                component={LoginForm}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/form"
                                component={App}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/perform"
                                component={PersonForm}
                                />
                            <Route exact path="/myfirstpage1.github.io/examples/searchf"
                                component={FilterProduct}
                                />
                            <Route path="/myfirstpage1.github.io/examples/news"
                                component={AppNews}
                                />
                            <Route path="/myfirstpage1.github.io/examples/file"
                                component={FileInput}
                                />
                            <Route component={NotFound}
                                />
                        </Switch>
                    </div>
                </Router>,
                document.getElementById("app"));